/* eslint-disable */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { reduxForm, Form, Field } from 'redux-form';
import InputAdornment from '@material-ui/core/InputAdornment';
import Email from '@material-ui/icons/Email';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import { getAuthInfoSelector } from '../../reducers/rootReducer';
import {
  setUsernameActionCreator,
  setPasswordActionCreator,
  setPasswordVisibilityActionCreator,
  handleRegisterActionCreator
} from '../../reducers/auth/authActionCreators';
import styles from './RegisterPageStyles';
/* eslint-enable */

export const PAGE_NAME = 'Sign up';

const FieldRenderer = ({
  input,
  meta,
  type,
  label,
  placeholder,
  className,
  value,
  onChange,
  InputProps
}) => <TextField
    fullWidth
    color="inherit"
    error={meta.touched && meta.error}
    type={type}
    label={label}
    placeholder={placeholder}
    className={className}
    value={value}
    onChange={onChange}
    InputProps={InputProps}
    {...input}
  />;

const validate = (values) => {
  const errors = {};
  if (!values.email) {
    errors.email = 'Requires email.';
  }
  return errors;
};

const RegisterPageComponent = ({
  classes,
  authInfo,
  togglePasswordVisibility,
  handleRegister,
  handleSubmit,
  submitting
}) => <Form onSubmit={
        handleSubmit(values => handleRegister({
          username: values.email,
          password: values.password
        }))}
      >
    <DialogContent>
      <DialogContentText>
        to continue with ReBind.
      </DialogContentText>
      <br />
      <Field
        name="email"
        type="email"
        label="Email"
        placeholder="Enter your Email"
        className={classes.textField}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Email />
            </InputAdornment>
          )
        }}
        component={FieldRenderer}
      />
      <br />
      <Field
        name="password"
        type={authInfo.isPasswordVisible ? 'text' : 'password'}
        label="Password"
        placeholder="Enter your Password"
        className={classes.textField}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <IconButton
                style={{ paddingRight: '0px' }}
                color="inherit"
                aria-label="Toggle password visibility"
                onClick={() => togglePasswordVisibility(!authInfo.isPasswordVisible)}
              >
                {authInfo.isPasswordVisible ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          )
        }}
        component={FieldRenderer}
      />
      <br />
    </DialogContent>
    <DialogActions>
      <Button
        type="submit"
        label="Submit"
        style={styles}
        variant="contained"
        color="primary"
        className={classes.button}
        disabled={submitting}
      >
        Register
    </Button>
    </DialogActions>
  </Form>;


RegisterPageComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  authInfo: PropTypes.object.isRequired,
  togglePasswordVisibility: PropTypes.func.isRequired,
  handleRegister: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  submitting: PropTypes.bool.isRequired
};

RegisterPageComponent.defaultProps = {
  authInfo: {
    username: '',
    password: ''
  }
};

const RegisterPageForm = reduxForm({
  form: 'register',
  validate
})(RegisterPageComponent);
const StyledRegisterPageForm = withStyles(styles)(RegisterPageForm);
const mapStateToContentProps = state => ({
  authInfo: getAuthInfoSelector(state)
});
const mapDispatchToContentProps = dispatch => ({
  togglePasswordVisibility: isVisible => dispatch(setPasswordVisibilityActionCreator(isVisible)),
  handleRegister: authInfo => new Promise(() => dispatch(handleRegisterActionCreator(authInfo)))
});
const ReduxRegisterPage = connect(
  mapStateToContentProps,
  mapDispatchToContentProps
)(StyledRegisterPageForm);

export default ReduxRegisterPage;
